        const link = svg.append("g")
                .attr("class", "links")
                .selectAll("line")
                .data(graph.links)
                .enter().append("line")
                // eslint-disable-next-line no-unused-vars
                .attr("stroke-width", function (d) {
                  // return Math.sqrt(d.value);
                  return 1;
                });

        const node = svg.append("g")
                .attr("class", "nodes")
                .selectAll("circle")
                .data(graph.nodes)
                .enter().append("circle")
                .attr("r", function (d) {
                  return d.size //圆圈大小
                })
                .attr("fill", function (d) {
                  // return color(d.group);
                  return colors[d.group];  //实体类型
                })
                .attr('stroke', 'none')
                .attr('name', function (d) {
                  return d.id;
                })
                .call(d3.drag()
                        .on("start", dragstarted)
                        .on("drag", dragged)
                        .on("end", dragended));

        const text = svg.append("g")
                .attr("class", "texts")
                .selectAll("text")
                .data(graph.nodes)
                .enter().append("text")
                .attr("font-size", function (d) {
                  return d.size
                })
                .attr("fill", function (d) {
                  // return color(d.group);
                  return colors[d.group];
                })
                .attr('name', function (d) {
                  return d.id;
                })
                .text(function (d) {
                  return d.id;
                })
                .attr('text-anchor', 'middle')
                .call(d3.drag()
                        .on("start", dragstarted)
                        .on("drag", dragged)
                        .on("end", dragended));

        node.append("title")
                .text(function (d) {
                  return d.id;
                });

        simulation
                .nodes(graph.nodes)
                .on("tick", ticked);

        simulation.force("link")
                .links(graph.links);

        function ticked() {
          link
                  .attr("x1", function (d) {
                    return d.source.x;
                  })
                  .attr("y1", function (d) {
                    return d.source.y;
                  })
                  .attr("x2", function (d) {
                    return d.target.x;
                  })
                  .attr("y2", function (d) {
                    return d.target.y;
                  });

          node
                  .attr("cx", function (d) {
                    return d.x;
                  })
                  .attr("cy", function (d) {
                    return d.y;
                  });

          text.attr('transform', function (d) {
            return 'translate(' + d.x + ',' + (d.y + d.size / 2) + ')';
          });
        }

        let dragging = false;

        function dragstarted(d) {
          if (!d3.event.active) simulation.alphaTarget(0.3).restart();
          d.fx = d.x;
          d.fy = d.y;
          dragging = true;
        }

        function dragged(d) {
          d.fx = d3.event.x;
          d.fy = d3.event.y;
        }

        function dragended(d) {
          if (!d3.event.active) simulation.alphaTarget(0);
          d.fx = null;
          d.fy = null;
          dragging = false;
        }

        // eslint-disable-next-line no-unused-vars
        $('#mode span').click(function (event) {
          $('#mode span').removeClass('active');
          $(this).addClass('active');
          if ($(this).text() == 'Circles') {
            $('.texts text').hide();
            $('.nodes circle').show();
          } else {
            $('.texts text').show();
            $('.nodes circle').hide();
          }
        });

        // eslint-disable-next-line no-unused-vars
        $('#svg1').on('mouseenter', '.nodes circle', function (event) {
          if (!dragging) {
            var name = $(this).attr('name');

            $('#info h4').css('color', $(this).attr('fill')).text(name);
            $('#info p').remove();
            for (var key in info[name]) {
              if (typeof (info[name][key]) == 'object') {
                continue;
              }
              if (key == 'id' || key == 'class' || key == 'name') {
                continue;
              }
              $('#info').append('<p><span>' + key + '</span>' + info[name][key] + '</p>');
            }

            d3.select('#svg1 .nodes').selectAll('circle').attr('class', function (d) {
              if (d.id == name) {
                return '';
              }

              for (var i = 0; i < graph.links.length; i++) {
                if (graph.links[i]['source'].id == name && graph.links[i]['target'].id == d.id) {
                  return '';
                }
                if (graph.links[i]['target'].id == name && graph.links[i]['source'].id == d.id) {
                  return '';
                }
              }
              return 'inactive';
            });
            d3.select("#svg1 .links").selectAll('line').attr('class', function (d) {
              if (d.source.id == name || d.target.id == name) {
                return '';
              } else {
                return 'inactive';
              }
            });
          }
        });

        // eslint-disable-next-line no-unused-vars
        $('#svg1').on('mouseleave', '.nodes circle', function (event) {
          if (!dragging) {
            d3.select('#svg1 .nodes').selectAll('circle').attr('class', '');
            d3.select('#svg1 .links').selectAll('line').attr('class', '');
          }
        });

        // eslint-disable-next-line no-unused-vars
        $('#svg1').on('mouseenter', '.texts text', function (event) {
          if (!dragging) {
            var name = $(this).attr('name');

            $('#info h4').css('color', $(this).attr('fill')).text(name);

            $('#info p').remove();
            for (var key in info[name]) {

              if (typeof (info[name][key]) == 'object') {
                continue;
              }
              if (key == 'id' || key == 'class' || key == 'name') {
                continue;
              }
              $('#info').append('<p><span>' + key + '</span>' + info[name][key] + '</p>');
            }
            //$('#info').append('<a href="/links"><p><span>' + '操作'+ '</span>' + '</p></a>s');

            d3.select('#svg1 .texts').selectAll('text').attr('class', function (d) {
              if (d.id == name) {
                return '';
              }

              for (var i = 0; i < graph.links.length; i++) {
                if (graph.links[i]['source'].id == name && graph.links[i]['target'].id == d.id) {
                  return '';
                }
                if (graph.links[i]['target'].id == name && graph.links[i]['source'].id == d.id) {
                  return '';
                }
              }
              return 'inactive';
            });
            d3.select("#svg1 .links").selectAll('line').attr('class', function (d) {
              if (d.source.id == name || d.target.id == name) {
                return '';
              } else {
                return 'inactive';
              }
            });
          }
        });

        // eslint-disable-next-line no-unused-vars
        $('#svg1').on('mouseleave', '.texts text', function (event) {
          if (!dragging) {
            d3.select('#svg1 .texts').selectAll('text').attr('class', '');
            d3.select('#svg1 .links').selectAll('line').attr('class', '');
          }
        });

        // eslint-disable-next-line no-unused-vars
        $('#search1 input').keyup(function (event) {
          if ($(this).val() == '') {
            d3.select('#svg1 .texts').selectAll('text').attr('class', '');
            d3.select('#svg1 .nodes').selectAll('circle').attr('class', '');
            d3.select('#svg1 .links').selectAll('line').attr('class', '');
          } else {
            var name = $(this).val();
            d3.select('#svg1 .nodes').selectAll('circle').attr('class', function (d) {
              if (d.id.toLowerCase().indexOf(name.toLowerCase()) >= 0) {
                return '';
              } else {
                return 'inactive';
              }
            });
            d3.select('#svg1 .texts').selectAll('text').attr('class', function (d) {
              if (d.id.toLowerCase().indexOf(name.toLowerCase()) >= 0) {
                return '';
              } else {
                return 'inactive';
              }
            });
            // eslint-disable-next-line no-unused-vars
            d3.select("#svg1 .links").selectAll('line').attr('class', function (d) {
              return 'inactive';
            });
          }
        });


        $('#svg2').on('mouseenter', 'g.row', function (event) {
          event.preventDefault();
          $('#svg2 text.title').attr('fill-opacity', 0);
          $('#svg2 text.title[name="' + $(this).attr('id') + '"]').attr('fill-opacity', 1);
        });

        $('#svg2').on('mouseleave', 'g.row', function (event) {
          event.preventDefault();
          $('#svg2 text.title').attr('fill-opacity', 0);
        });


      })

      },

    methods:{

      async getEntities(){
        let _this = this
        let succss = false

        await axios.get('/entities').then(resp=>{
          _this.entities = resp.data;
          succss = true
        })
        return succss
      }

    },
    created() {
      const _this = this
      axios.get('/e').then(resp=>{
        _this.entities = resp.data;

      })
    }


  }
</script>

